
import { defineComponent } from "vue";
import {
  useBoard,
  useEthers,
  useWallet,
  displayChainName,
  displayEther,
  shortenAddress,
} from "vue-dapp";

const { open } = useBoard();
const { status, disconnect, error } = useWallet();
const { address, balance, chainId, isActivated } = useEthers();

export default defineComponent({
  setup() {
    return {
      displayChainName,
      displayEther,
      shortenAddress,
      open,
      disconnect,
    };
  },
  data() {
    var chainIdd: any = chainId;

    return {
      status,
      error,
      address,
      balance,
      chainIdd,
      isActivated,
    };
  },
});
